amily: Arial, sans-serif;\n  font-size: 14px;\n}\n\n.news-container {\n  display: flex;\n}\n\n.news-col {\n  width: 55%;\n  padding-top: 0.5em;\n}\n\n.tabel-col {\n  width: 45%;\n}\n\n.news-list {\n  display: flex;\n  margin-bottom: 5px;\n}\n\n.news-image {\n  width: 180px;\n}\n\n.news-image img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  object-position: center;\n}\n\n.news-image svg {\n  max-width: 170px;\n  max-height: 106px;\n  width: 100%;\n  height: 100%;\n}\n\n.news-content {\n  padding: 0px 15px;\n  width: calc(100% - 180px);\n}\n\n.title {\n  margin-top: 0;\n  margin-bottom: 8px;\n  color: white;\n  background-color: transparent;\n  font-size: 16px;\n}\n\n.Money {\n  background-color: transparent;\n  color: white;\n}\n\n.align-items-center {\n  height: 400px;\n}\n\n.date {\n  margin-top: 0;\n  margin-bottom: 8px;\n  font-size: 12px;\n}\n\n.description {\n  overflow: hidden;\n  /* display: -webkit-box; */\n  -webkit-line-clamp: 1;\n  -webkit-box-orient: vertical;\n  margin-top: 8px;\n  margin-bottom: 8px;\n}\n\n.link {\n  text-decoration: unset;\n  color: #d9b08c;\n}\n\n.link:hover {\n  color: #922a31;\n}\n\n.tabel-slider {\n  position: relative;\n  display: flex;\n  flex-wrap: wrap;\n}\n\n.tabel-slider .slide {\n  flex: 0 0 49.5%;\n  max-width: 49.5%;\n  overflow: auto;\n  max-height: 350px;\n  scrollbar-width: thin;\n  scrollbar-color: #f5f5f5;\n}\n/*FireFox Scrollbar*/\n\n.tabel-slider .slide:-webkit-scrollbar {\n  width: 3px;\n}\n\n.tabel-slider .slide:-webkit-scrollbar-track {\n  background: black;\n}\n\n.tabel-slider .slide:-webkit-scrollbar-thumb {\n  background-color: #000;\n  border-radius: 0px;\n  border: 0px solid transparent;\n}\n\n.tabel-slider .slide::-webkit-scrollbar-track {\n  background-color: black;\n}\n\n.tabel-slider .slide::-webkit-scrollbar {\n  width: 5px;\n  background-color: black;\n}\n\n.tabel-slider .slide::-webkit-scrollbar-thumb {\n  background-color: black;\n}\n\n.tabel-slider .slide:nth-child(odd) {\n  margin-right: auto;\n}\n\n.tabel-slider img {\n  width: 100%;\n}\n\n.previous,\n.next {\n  cursor: pointer;\n  position: absolute;\n  top: 3px;\n  margin-top: 0;\n  width: 15px;\n  height: 15px;\n  padding: 0;\n  display: none;\n}\n\n.previous svg,\n.next svg {\n  width: 12px;\n  height: 12px;\n}\n\n.previous {\n  right: 20px;\n}\n\n.next {\n  right: 0;\n}\n\n@media (max-width: 767px) {\n  .news-container {\n    display: block;\n  }\n  .col {\n    width: 100%;\n  }\n  .news-content {\n    padding: 0px 15px;\n  }\n  .tabel-slider .slide {\n    flex: 0 0 100%;\n    max-width: 100%;\n  }\n}\n\n@media (max-width: 480px) {\n  .news-list {\n    display: block;\n  }\n  .news-image,\n  .news-content {\n    width: 100%;\n  }\n  .news-content {\n    padding: 0;\n    margin-top: 5px;\n  }\n  .news-image svg {\n    display: block;\n    margin: auto;\n  }\n}\n\n* {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n  outline: none;\n  text-decoration: none;\n  background-color: black;\n}\n\n.sportNewsSection,\n.newsSection,\n.news-container {\n  padding: 0.2rem;\n  color: white;\n}\n\n.header {\n  padding: 0.5rem;\n}\n\n.video {\n  padding: 1rem 0.5rem 0.5rem 0;\n}\n\n.third,\n.fourth {\n  padding-top: 0.5rem;\n}\n</style>\n<style>\n *,\n *::before,\n *::after {\n   box-sizing: border-box;\n }\n\nbody {\n  font-family: \"Work Sans\", sans-serif;\n  font-weight: 400;\n  height: 100vh;\n}\n\n.wrapper {\n  height: 100%;\n  width: 100%;\n  display: flex;\n  justify-content: center;\n}\n\n.carousel {\n  position: relative;\n  width: 100%;\n  max-width: 500px;\n  display: flex;\n  justify-content: center;\n  flex-direction: column;\n}\n\n.carousel__item {\n  background-color: transparent;\n  display: flex;\n  align-items: center;\n  position: absolute;\n  width: 100%;\n  padding: 0 12px;\n  opacity: 0;\n  filter: drop-shadow(0 2px 2px #555);\n  will-change: transform, opacity;\n  -webkit-animation: carousel-animate-vertical 27s linear infinite;\n  animation: carousel-animate-vertical 27s linear infinite;\n}\n\n.carousel__item:nth-child(1) {\n  -webkit-animation-delay: calc(3s * -1);\n  animation-delay: calc(3s * -1);\n}\n\n.carousel__item:nth-child(2) {\n  -webkit-animation-delay: calc(3s * 0);\n  animation-delay: calc(3s * 0);\n}\n\n.carousel__item:nth-child(3) {\n  -webkit-animation-delay: calc(3s * 1);\n  animation-delay: calc(3s * 1);\n}\n\n.carousel__item:nth-child(4) {\n  -webkit-animation-delay: calc(3s * 2);\n  animation-delay: calc(3s * 2);\n}\n\n.carousel__item:nth-child(5) {\n  -webkit-animation-delay: calc(3s * 3);\n  animation-delay: calc(3s * 3);\n}\n\n.carousel__item:nth-child(6) {\n  -webkit-animation-delay: calc(3s * 4);\n  animation-delay: calc(3s * 4);\n}\n\n.carousel__item:nth-child(7) {\n  -webkit-animation-delay: calc(3s * 5);\n  animation-delay: calc(3s * 5);\n}\n\n.carousel__item:nth-child(8) {\n  -webkit-animation-delay: calc(3s * 6);\n  animation-delay: calc(3s * 6);\n}\n\n.carousel__item:last-child {\n  -webkit-animation-delay: calc(-3s * 2);\n  animation-delay: calc(-3s * 2);\n}\n\n.carousel__item-head {\n  border-radius: 50%;\n  background-color: #f0b90b;\n  border-color: inherit;\n  height: 90px;\n  padding: 14px;\n  position: relative;\n  margin-right: -45px;\n  flex-shrink: 0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 50px;\n}\n\n.carousel__item-body {\n  width: 100%;\n  background-color: #202020;\n  border-radius: 8px;\n  padding: 16px 20px 16px 70px;\n}\n\n.title {\n  text-transform: uppercase;\n  font-size: 20px;\n  margin-top: 10px;\n}\n\n@-webkit-keyframes carousel-animate-vertical {\n  0% {\n    transform: translateY(50%) scale(0.5);\n    opacity: 0;\n    visibility: hidden;\n  }\n  3%,\n  11.1111111111% {\n    transform: translateY(50%) scale(0.7);\n    opacity: 0;\n    visibility: visible;\n  }\n  14.1111111111%,\n  22.2222222222% {\n    transform: translateY(0) scale(1);\n    opacity: 1;\n    visibility: visible;\n  }\n  25.2222222222%,\n  33.3333333333% {\n    transform: